body {
  @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500;600;700;800;900&display=swap');
  $font-default: 'Montserrat';
  $font-min: 12px;
  $font-reg: 14px;
  $font-med: 21px;
  background-color: #f2f2f2;
  display: grid;
  align-items: space-between;
  position: relative;
  margin: 0;
  padding: 0;
  font-family: $font-default;
  font-size: $font-reg;

  .font-med {
    font-size: $font-med;
  }

  header {
    height: 80vh;
    width: 100%;
    background-image: url('/src/assets/img/hikemain.webp');
    background-size: cover;
    background-position: bottom right;

    .maincontainer {
      width: 60%;
      height: 100%;
      margin: 0 auto;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: 72px 1fr 2fr;
      grid-column-gap: 0px;
      grid-row-gap: 0px; 
    
      #weatherwidget {
        grid-area: 1 / 1 / 2 / 3;
        display: flex;
        align-items: center;
        margin-top: 24px;
        padding-left: 20px;
        height: 48px;
        width: 100%;
        background-color: rgb(255,255,255,0.8);
        border-radius: 3px;
        overflow: hidden;
        position: relative;
        font-family: $font-default;
        font-size: $font-min;

        .find-location {
          height: 30px;
          width: 30px;
          margin: 0 10px;
          background-color: #333333;
          border: 0;
          border-radius: 30px;
          cursor: pointer;
          transition: 0.2s ease-in-out;
        
          &:hover {
            background-color: #666666;
          }

          .iconstyle {
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 18px;
            color: #fff;
          }
        }

        .city {
          font-weight: 600;
        }

        #temp, #temp2 {
          display: flex;
          align-items: center;
          margin-right: 6px;
        }

        #weather {
          position: absolute;
          z-index: 2;
          height: inherit;
          display: flex;
          align-items: center;
          left: 190px;

          .addred {
            color: #d01630;
          }

          .addorange {
            color: #d77e0c;
          }
      
          .addblue {
            color: #1193e1;
          }
        }

        #weatherwarsaw {
          display: flex;
          position: absolute;
          left: 190px;
          transition: 1s;
          align-items: center;
          z-index: 1;
          height: inherit;
      
          .addred {
            color: #d01630;
          }

          .addorange {
            color: #d77e0c;
          }
      
          .addblue {
            color: #1193e1;
          }
      }



        .iconcondition {
          height: 36px;
          width: 36px;
          margin: 0 6px;
        
          img {
            height: inherit;
            width: inherit;        
          }
        }

        .rightbox {
          width: 48px;
          height: 48px;
          background-color: #333333;
        }
      }
    }

    .div2 { grid-area: 2 / 1 / 3 / 2; }
    .div3 { grid-area: 2 / 2 / 3 / 3; }
    .div4 { 
      grid-area: 3 / 1 / 4 / 2;
      background-image: url('/src/assets/img/ryhtext.png');
      background-repeat: no-repeat;
      background-size: 100%;
      background-position: top right;
    }
    .div5 { grid-area: 3 / 2 / 4 / 3; } 
  }

  main {
    width: 100%;
  }

  main.mainarea {
    height: 15vh;
    display: grid;
    align-items: center;
  }


  .container {
    width: 60%;
    margin: 0 auto;    
  }

  #formFirstStep {
    display: flex;
    justify-content: space-between;
  }


  footer {
    background-color: #ffffff;
    height: 5vh;
    min-height: 48px;
    width: 100%;
    font-size: $font-min;
  }

  footer.mtn {
    width: 100%;
    background-image: url('/src/assets/img/mtnfooter.png');
    height: 300px;
  }

}
